import React, { useContext, useEffect, useRef } from 'react';
import { Container, Row, Button } from "reactstrap";
import { NavLink, Link, useNavigate } from "react-router-dom";
import logo from "../../Assets/images/logo.png";
import "../../Assets/css/Header.css";
import "./StickyHeader.css";
import { AuthContext } from '../../context/AuthContext';

const nav__links = [
    {
        path: "/home",
        display: "Home"
    },
    {
        path: "/about",
        display: "About"
    },
    {
        path: "/tours",
        display: "Tours"
    },
]

const Header = () => {
    const headerRef = useRef(null);
    const navigate = useNavigate();
    const { user, dispatch } = useContext(AuthContext);
    const logout = () => {
        dispatch({ type: "LOGOUT" });
        navigate("/");
    }
    const stickyHeaderFunction = () => {
        window.addEventListener("scroll", () => {
            if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80) {
                headerRef.current.classList.add("sticky__header")
            } else {
                headerRef.current.classList.remove("sticky__header")
            }
        })
    }
    useEffect(() => {
        stickyHeaderFunction()
        return window.removeEventListener("scroll", stickyHeaderFunction)
    })
    //     const stickyHeaderFunction = () => {
    //         window.addEventListener("scroll", () => {
    //             if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80) {
    //                 headerRef.current.style.width = "100%";
    //                 headerRef.current.style.height = "80px";
    //                 headerRef.current.style.lineHeight = "80px";
    //                 headerRef.current.style.background = "#FFF";
    //                 headerRef.current.style.position = "sticky";
    //                 headerRef.current.style.top = "0";
    //                 headerRef.current.style.left = "0";
    //                 headerRef.current.style.boxShadow = "3px 3px 8px -3px #ddd";
    //                 headerRef.current.style.zIndex = "999";
    //             } else {
    //                 headerRef.current.style = null;
    //             }
    //         })
    //     }

    //     useEffect(() => {
    //         stickyHeaderFunction()
    //         return () => {
    //             window.removeEventListener("scroll", stickyHeaderFunction)
    //         };
    //     }, [])

    return (
        <header className='header' ref={headerRef}>
            <Container>
                <Row>
                    <div className="nav__wrapper d-flex align-items-center justify-content-between">
                        <div className="logo">
                            <img src={logo} alt='' />
                        </div>
                        <div className="navigation">
                            <ul className="menu d-flex align-items-center gap-5">
                                {
                                    nav__links?.map((item, index) => {
                                        return (
                                            <li className='nav__item' key={index}>
                                                <NavLink to={item.path} className={navClass => navClass.isActive ? "active__link" : ""}>{item.display}</NavLink>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </div>
                        <div className="nav__right d-flex align-items-center gap-4">
                            <div className="nav__btns d-flex align-items-center gap-4">
                                {user ? (
                                    <>
                                        <h5 className='mb-0'>{user.username}</h5>
                                        <Button className='btn btn-dark' onClick={logout}>Logout</Button>
                                    </>
                                ) : (
                                    <>
                                        <Button className='btn secondary__btn'><NavLink to="/login" className={navClass => navClass.isActive ? "text__primary-active" : ""}>Login</NavLink></Button>
                                        <Button className='btn primary__btn'><Link to="/register">Register</Link></Button>
                                    </>
                                )}
                            </div>
                            <span className="mobile__menu">
                                <i className="ri-menu-line"></i>
                            </span>
                        </div>
                    </div>
                </Row>
            </Container>
        </header>
    )
}

export default Header;