import React from 'react';
import SweetAlert2 from 'react-sweetalert2';

const SweetAlert = ({ show, title, text, icon, onConfirm }) => {
  return (
    <SweetAlert2
      show={show}
      title={title}
      text={text}
      icon={icon}
      onConfirm={onConfirm}
      allowOutsideClick={onConfirm}
    />
  );
};

export default SweetAlert;