import React, { useContext, useState } from 'react';
import {useNavigate} from "react-router-dom";
import "../../Assets/css/booking.css";
import { Button, Form, FormGroup, ListGroup, ListGroupItem } from 'reactstrap';
import SweetAlert from '../../shared/SweetAlert';
import { AuthContext } from '../../context/AuthContext';
import { BASE_URL } from '../../utilities/config';

const Booking = ({ tour, avgRating }) => {
    const { price, reviews,title } = tour;
    const navigate = useNavigate();
    const {user} = useContext(AuthContext);
    const [swalProps, setSwalProps] = useState({
        show: false,
        title: "",
        text: "",
        icon: "",
      });

    const [booking,setBooking] = useState({
        userId:user && user._id,
        userEmail:user && user.email,
        tourName:title,
        phone:"",
        guestSize : 1,
        bookAt:""
    })

    const handleChange=(e)=>{
        setBooking((prev)=>({...prev,[e.target.id]:e.target.value}))
    }

    const handleClick = async(event) => {
        event.preventDefault();

        // Check for empty fields
        const emptyFields = Object.entries(booking).filter(([key, value]) => value === "");
        if (!user || user === undefined || user === null || user === "") {
            return setSwalProps({
              show: true,
              title: "Error",
              text: "Please SignIn.",
              icon: "error",
            });
          }else if (emptyFields.length > 0) {
            setSwalProps({
                show: true,
                title: "Error",
                text: "Please fill in all fields.",
                icon: "error",
              });
        } else {
            try {
                const res = await fetch(`${BASE_URL}/booking/create-booking`, {
                    method: "POST",
                    headers: {
                        'Content-Type': "application/json"
                    },
                    credentials: "include",
                    body: JSON.stringify(booking)
                });
                const result = await res.json();
                if (!res.ok) {
                    return setSwalProps({
                        show: true,
                        title: "Error",
                        text: result.message,
                        icon: "error",
                    });
                }
                navigate("/thank-you") 
            } catch (error) {
                setSwalProps({
                    show: true,
                    title: "Error",
                    text: error.message,
                    icon: "error",
                  });
            }
        }
    }

    const serviceFee = 10;
    const totalAmount = Number(price) * Number(booking.guestSize) + Number(serviceFee);

    const handleAlertConfirm = () => {
        setSwalProps({ show: false }); // Reset the show property to hide the alert
      };

    return (
        <div className='booking'>
            <div className="booking__top d-flex align-items-center justify-content-between">
                <h3>${price} <span>/per person</span></h3>
                <span className="tour__rating d-flex align-items-center">
                    <i
                        className="ri-star-fill"
                    ></i>
                    {avgRating === 0 ? null : avgRating} ({reviews?.length})
                </span>
            </div>
            <div className="booking__form">
                <h5>Information</h5>
                <Form className='booking__info-form'>
                    <FormGroup>
                        <input type="text" placeholder='Full Name' id='fullName' onChange={handleChange}/>
                    </FormGroup>
                    <FormGroup>
                        <input type="number" placeholder='Phone' id='phone' onChange={handleChange}/>
                    </FormGroup>
                    <FormGroup className='d-flex align-items-center gap-3'>
                        <input type="date" placeholder='' id='bookAt' onChange={handleChange}/>
                        <input type="number" placeholder='Guest' id='guestSize' onChange={handleChange}/>
                    </FormGroup>
                </Form>
            </div>
            <div className="booking__bottom">
                <ListGroup>
                    <ListGroupItem className='border-0 px-0'>
                        <h5 className='d-flex align-items-center gap-1'>${price} <i className="ri-close-line"></i> 1 person</h5>
                        <span> ${price}</span>
                    </ListGroupItem>
                    <ListGroupItem className='border-0 px-0'>
                        <h5>Service charge</h5>
                        <span> ${serviceFee}</span>
                    </ListGroupItem>
                    <ListGroupItem className='border-0 px-0 total'>
                        <h5>Total</h5>
                        <span> ${totalAmount}</span>
                    </ListGroupItem>
                </ListGroup>
                <SweetAlert {...swalProps} onConfirm={handleAlertConfirm} />
                <Button className='btn primary__btn w-100 mt-4' onClick={handleClick}>Book Now</Button>
            </div>
        </div>
    )
}

export default Booking;