import React, { useContext, useState } from 'react';
import { container, Col, Row, Container, Form, FormGroup, Button } from "reactstrap";
import "../../Assets/css/login.css";
import loginImg from "../../Assets/images/login.png";
import userIcon from "../../Assets/images/user.png";
import { Link, useNavigate } from 'react-router-dom';
import SweetAlert from '../../shared/SweetAlert';
import { BASE_URL } from '../../utilities/config';
import { AuthContext } from '../../context/AuthContext';

const Login = () => {
  const navigate = useNavigate();

  const { dispatch } = useContext(AuthContext);

  const [swalProps, setSwalProps] = useState({
    show: false,
    title: "",
    text: "",
    icon: "",
  });

  const [credentials, setCredentials] = useState({
    email: undefined,
    password: undefined
  })

  const handleClick = async (event) => {
    event.preventDefault();
    const emptyFields = Object.entries(credentials).filter(([key, value]) => value === "" || value === undefined);
    
    if (emptyFields.length !== 0) {
      setSwalProps({
        show: true,
        title: "Error",
        text: "Please fill in all fields.",
        icon: "error",
      });
    } else {
      dispatch({type:"LOGIN_START"})
      try {
        const res = await fetch(`${BASE_URL}/auth/login`,{
          method:"POST",
          headers:{
            'Content-Type':"application/json"
          },
          credentials:"include",
          body:JSON.stringify(credentials)
        });
        const result = await res.json();
        if (!res.ok) {
          return setSwalProps({
            show: true,
            title: "Error",
            text: result.message,
            icon: "error",
          });
        }
        dispatch({type:"LOGIN_SUCCESS",payload:result.data})
        navigate("/")
      } catch (error) {
        dispatch({type:"LOGIN_FAILURE",payload:error.message})
        setSwalProps({
          show: true,
          title: "Error",
          text: error.message,
          icon: "error",
        });
      }
    }
  }

  const handleChange = (e) => {
    setCredentials((prev) => ({ ...prev, [e.target.id]: e.target.value }))
  }

  const handleAlertConfirm = () => {
    setSwalProps({ show: false }); // Reset the show property to hide the alert
  };
  return (
    <section>
      <Container>
        <Row>
          <Col lg="8" className='m-auto'>
            <div className="login__container d-flex justify-content-between">
              <div className="login__img">
                <img src={loginImg} alt="log-in-img" />
              </div>
              <div className="login__form">
                <div className="user">
                  <img src={userIcon} alt="user-avatar" />
                </div>
                <h2>Login</h2>
                <Form onSubmit={handleClick}>
                  <FormGroup>
                    <input type="email" placeholder='Email' id='email' onChange={handleChange} />
                  </FormGroup>
                  <FormGroup>
                    <input type="password" placeholder='Password' id='password' onChange={handleChange} />
                  </FormGroup>
                  <Button className='btn secondary__btn auth__btn' type='submit'>
                    Login
                  </Button>
                  <p>Don't have an account? <Link to={"/register"}>Create</Link></p>
                  <SweetAlert {...swalProps} onConfirm={handleAlertConfirm} />
                </Form>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  )
}

export default Login;