import React, { useRef, useState } from 'react';
import {Col,Form,FormGroup} from "reactstrap";
import "../Assets/css/SearchBar.css";
import SweetAlert2 from 'react-sweetalert2';
import SweetAlert from './SweetAlert';
import { useNavigate } from 'react-router-dom';
import { BASE_URL } from '../utilities/config';

const SearchBar = () => {
    const locationRef = useRef("");
    const distanceRef = useRef(0);
    const maxGroupSizeRef = useRef(0);
    const navigate = useNavigate();
    const [swalProps, setSwalProps] = useState({
      show: false,
      title: "",
      text: "",
      icon: "",
    });
  
    const searchHandler = async(event) => {
      event.preventDefault();
      const location = locationRef.current.value;
      const distance = distanceRef.current.value;
      const maxGroupSize = maxGroupSizeRef.current.value;
      if (location === "" || distance === "" || maxGroupSize === "") {
        return setSwalProps({
          show: true,
          title: "Error",
          text: "Please fill in all fields.",
          icon: "error",
        });
      }
      const res = await fetch(`${BASE_URL}/tour/search-tours?city=${location}&distance=${distance}&maxGroupSize=${maxGroupSize}`);
      if(!res.ok){
        return setSwalProps({
          show: true,
          title: "Error",
          text: "Something Went Wrong.",
          icon: "error",
        });
      }
      const result = await res.json()
      navigate(`/tours/search?city=${location}&distance=${distance}&maxGroupSize=${maxGroupSize}`,{state:result?.data})
    };
  
    const handleAlertConfirm = () => {
      setSwalProps({ show: false }); // Reset the show property to hide the alert
    };

  return (
    <Col>
        <div className="search__bar">
            <Form className='d-flex align-items-center gap-4' onSubmit={searchHandler}>
                <FormGroup className='d-flex gap-3 form__group form__group-fast'>
                    <span><i className="ri-map-pin-line"></i></span>
                    <div>
                        <h6>Location</h6>
                        <input type="text" placeholder='Where are you going?' ref={locationRef}/>
                    </div>
                </FormGroup>
                <FormGroup className='d-flex gap-3 form__group form__group-fast'>
                    <span><i className="ri-map-pin-time-line"></i></span>
                    <div>
                        <h6>Distance</h6>
                        <input type="number" placeholder='Distance k/m' ref={distanceRef}/>
                    </div>
                </FormGroup>
                <FormGroup className='d-flex gap-3 form__group form__group-last'>
                    <span><i className="ri-group-line"></i></span>
                    <div>
                        <h6>Max People</h6>
                        <input type="number" placeholder='0' ref={maxGroupSizeRef}/>
                    </div>
                </FormGroup>
                <button className="search__icon" type="submit">
                        <i className="ri-search-line"></i>
                    </button>
                    <SweetAlert {...swalProps} onConfirm={handleAlertConfirm} />
            </Form>
        </div>
    </Col>
  )
}

export default SearchBar;