import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ava01 from "../../Assets/images/ava-1.jpg";
import ava02 from "../../Assets/images/ava-2.jpg";
import ava03 from "../../Assets/images/ava-3.jpg";

const Testimonial = () => {
    const settings = {
        dots: true,
        infinite: true,
        autoplay: true,
        speed: 1000,
        swipeToSlide: true,
        autoplaySpeed: 2000,
        slidesToShow: 3,

        responsive: [
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: true,
                },
            },
            {
                breakpoint: 576,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                },
            },
        ],
    };

    return (
        <Slider {...settings}>
            <div className="testimonial py-4 px-3">
                <p>"The travel experience with this company was absolutely amazing. The destinations were breathtaking, and the guides were knowledgeable and friendly. Can't wait for my next adventure with them!"</p>
                <div className="d-flex align-items-center gap-4 mt-3">
                    <img src={ava01} className='w-25 h-25 rounded-2' alt='testimonial'/>
                    <div>
                        <h6 className='mb-0 mt-3'>Jane Smith</h6>
                        <p>Adventurer</p>
                    </div>
                </div>
            </div>
            <div className="testimonial py-4 px-3">
                <p>"I've been on many trips, but the one organized by this travel company was truly exceptional. Every detail was well taken care of, and the memories I've made will last a lifetime."</p>
                <div className="d-flex align-items-center gap-4 mt-3">
                    <img src={ava02} className='w-25 h-25 rounded-2' alt='testimonial'/>
                    <div>
                        <h6 className='mb-0 mt-3'>Michael Johnson</h6>
                        <p>Explorer</p>
                    </div>
                </div>
            </div>
            <div className="testimonial py-4 px-3">
                <p>"The tour guides were not only informative but also passionate about the places we visited. I felt like I was discovering the world with a knowledgeable friend. Highly recommended!"</p>
                <div className="d-flex align-items-center gap-4 mt-3">
                    <img src={ava03} className='w-25 h-25 rounded-2' alt='testimonial'/>
                    <div>
                        <h6 className='mb-0 mt-3'>Emily Anderson</h6>
                        <p>Travel Enthusiast</p>
                    </div>
                </div>
            </div>
            <div className="testimonial py-4 px-3">
                <p>"As a solo traveler, safety is a top priority for me. This travel agency ensured that I had a secure and enjoyable experience throughout my journey. I can't wait to embark on my next solo adventure with them!"</p>
                <div className="d-flex align-items-center gap-4 mt-3">
                    <img src={ava01} className='w-25 h-25 rounded-2' alt='testimonial'/>
                    <div>
                        <h6 className='mb-0 mt-3'>Alex Turner</h6>
                        <p>Solo Traveler</p>
                    </div>
                </div>
            </div>
        </Slider>
    )
}

export default Testimonial;
