import React, { useState, useContext } from 'react';
import { container, Col, Row, Container, Form, FormGroup, Button } from "reactstrap";
import "../../Assets/css/login.css";
import registerImg from "../../Assets/images/register.png";
import userIcon from "../../Assets/images/user.png";
import { Link, useNavigate } from 'react-router-dom';
import SweetAlert from '../../shared/SweetAlert';
import { AuthContext } from '../../context/AuthContext';
import { BASE_URL } from '../../utilities/config';

const Register = () => {
  const navigate = useNavigate();
  const [swalProps, setSwalProps] = useState({
    show: false,
    title: "",
    text: "",
    icon: "",
  });

  const [credentials, setCredentials] = useState({
    username: undefined,
    email: undefined,
    password: undefined
  })

  const { dispatch } = useContext(AuthContext);

  const handleClick = async (event) => {
    event.preventDefault();
    const emptyFields = Object.entries(credentials).filter(([key, value]) => value === "");

    if (emptyFields.length !== 0) {
      setSwalProps({
        show: true,
        title: "Error",
        text: "Please fill in all fields.",
        icon: "error",
      });
    } else {
      try {
        const res = await fetch(`${BASE_URL}/auth/register`,{
          method:"POST",
          headers:{
            'Content-Type':"application/json"
          },
          body:JSON.stringify(credentials)
        });
        const result = await res.json()
        if (!res.ok) {
          setSwalProps({
            show: true,
            title: "Error",
            text: result.message,
            icon: "error",
          });
        }
        dispatch({type:"REGISTER_SUCCESS"})
        navigate("/login")
      } catch (error) {
        setSwalProps({
          show: true,
          title: "Error",
          text: error.message,
          icon: "error",
        });
      }
    }
  }

  const handleChange = (e) => {
    setCredentials((prev) => ({ ...prev, [e.target.id]: e.target.value }))
  }

  const handleAlertConfirm = () => {
    setSwalProps({ show: false });
  };
  
  return (
    <section>
      <Container>
        <Row>
          <Col lg="8" className='m-auto'>
            <div className="login__container d-flex justify-content-between">
              <div className="login__img">
                <img src={registerImg} alt="log-in-img" />
              </div>
              <div className="login__form">
                <div className="user">
                  <img src={userIcon} alt="user-avatar" />
                </div>
                <h2>Register</h2>
                <Form onSubmit={handleClick}>
                  <FormGroup>
                    <input type="text" placeholder='Username' id='username' onChange={handleChange} />
                  </FormGroup>
                  <FormGroup>
                    <input type="email" placeholder='Email' id='email' onChange={handleChange} />
                  </FormGroup>
                  <FormGroup>
                    <input type="password" placeholder='Password' id='password' onChange={handleChange} />
                  </FormGroup>
                  <Button className='btn secondary__btn auth__btn' type='submit'>
                    Create Account
                  </Button>
                  <p>Already have an account? <Link to={"/login"}>Login</Link></p>
                  <SweetAlert {...swalProps} onConfirm={handleAlertConfirm} />
                </Form>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  )
}

export default Register;