import React, { useEffect, useState } from 'react'
import CommonSection from '../../shared/CommonSection';
import "../../Assets/css/tours.css";
import tourData from "../../Assets/data/tours";
import { Col, Container, Row } from 'reactstrap';
import SearchBar from '../../shared/SearchBar';
import TourCard from '../../shared/TourCard';
import NewsLetter from '../../shared/NewsLetter';
import useFetch from '../../hooks/useFetch';
import { BASE_URL } from '../../utilities/config';
import SweetAlert from '../../shared/SweetAlert';
import { Bars } from 'react-loader-spinner';

const Tours = () => {
  const [pageCount, setPageCount] = useState(0);
  const [page, setpage] = useState(0);
  const { data: tours, error, loading, handleFetchError } = useFetch(`${BASE_URL}/tour/get-tour?page=${page}`);
  const { data: tourCount } = useFetch(`${BASE_URL}/tour/get-tour-count`);

  const showErrorAlert = () => {
    return (
      <SweetAlert
        show={error !== null}
        title="Error"
        text={error}
        icon="error"
        onConfirm={handleFetchError}
      />
    );
  };

  useEffect(() => {
    const pages = Math.ceil(tourCount / 8);
    setPageCount(pages);
    window.scrollTo(0,0);
  }, [page, tourCount, tours]);

  return (
    <>
      <CommonSection title={"All Tours"} />
      <section>
        <Container>
          <Row>
            <Col>
              <SearchBar />
            </Col>
          </Row>
        </Container>
      </section>
      <section className='pt-0'>
        <Container>
          <Row>
            {loading ? (
              <Col className='d-flex justify-content-center align-items-center'>
                <Bars
                  height="200"
                  width="100"
                  color="#f8a935"
                  ariaLabel="bars-loading"
                  wrapperStyle={{}}
                  wrapperClass=""
                  visible={true}
                />
              </Col>
            ) : (
              <>
                {tours?.map((tour, index) => {
                  return (
                    <Col key={tour._id} lg="3" className='mb-4'>
                      <TourCard tour={tour} />
                    </Col>
                  )
                })}
                <Col lg="12">
                  <div className="pagination d-flex align-items-center justify-content-center mt-4 gap-3">
                    {[...Array(pageCount).keys()].map((number, index) => {
                      return (
                        <span key={number} onClick={() => setpage(number)} className={page === number ? "active__page" : ""}>
                          {number + 1}
                        </span>
                      )
                    })}
                  </div>
                </Col>
              </>
            )}
          </Row>
        </Container>
      </section>
      <NewsLetter />
      {showErrorAlert()}
    </>
  )
}

export default Tours;