import React from "react";
import "../Assets/css/newsletter.css";
import maleTourist from "../Assets/images/male-tourist.png";
import { Col, Container, Row } from "reactstrap";

const NewsLetter = () => {
  return (
    <section className="newsletter">
      <Container>
        <Row>
          <Col lg="6">
            <div className="newsletter__content">
              <h2>Subscribe now to get usefull traveling information.</h2>
              <div className="newsletter__input">
                <input type="email" placeholder="Enter your email" />
                <button className="btn newsletter__btn">Subscribe</button>
              </div>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Eum
                veniam ex inventore vitae voluptates dolorum laborum quasi
                tempora hic minus. Quis officiis adipisci laborum, nihil velit
                molestias perspiciatis assumenda placeat!
              </p>
            </div>
          </Col>
          <Col lg="6">
            <div className="newsletter__img">
              <img src={maleTourist} alt="" />
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default NewsLetter;
