import React, { useContext, useEffect, useRef, useState } from "react";
import "../../Assets/css/tourDetails.css";
import tourData from "../../Assets/data/tours";
import { useParams } from "react-router-dom";
import { Col, Container, Form, ListGroup, Row } from "reactstrap";
import calculatedAvgRating from "../../utilities/avgRating";
import avatar from "../../Assets/images/avatar.jpg";
import SweetAlert from "../../shared/SweetAlert";
import Booking from "../../components/Booking/Booking";
import useFetch from "../../hooks/useFetch";
import { BASE_URL } from "../../utilities/config";
import { Dna } from "react-loader-spinner";
import { AuthContext } from "../../context/AuthContext";

const TourDetails = () => {
  const { id } = useParams();
  const [reviewText, setReviewText] = useState("");
  const [rating, setTourRating] = useState(null);
  const [ratingIcons, setRatingIcons] = useState([
    "ri-star-s-line",
    "ri-star-s-line",
    "ri-star-s-line",
    "ri-star-s-line",
    "ri-star-s-line",
  ]);
  const handleRatingClick = (index) => {
    const updatedRatingIcons = ratingIcons && ratingIcons.map((iconClass, i) =>
      i <= index ? "ri-star-s-fill" : "ri-star-s-line"
    );
    setTourRating(index + 1); // Update the rating state (1-based index)
    setRatingIcons(updatedRatingIcons); // Update the ratingIcons state
  };
  const { user } = useContext(AuthContext);
  const { data: tour, error, loading, handleFetchError } = useFetch(`${BASE_URL}/tour/get-single-tour/${id}`);
  const [reviews, setReviews] = useState(tour.reviews || []);
  useEffect(() => {
    if (tour.reviews) {
      setReviews(tour.reviews);
    }
  }, [tour]);
  const { photo, title, desc, price, address, city, distance, maxGroupSize, } = tour;
  const { totalRating, avgRating } = calculatedAvgRating(reviews);
  const [swalProps, setSwalProps] = useState({
    show: false,
    title: "",
    text: "",
    icon: "",
  });
  const showErrorAlert = () => {
    return (
      <SweetAlert
        show={error !== null}
        title="Error"
        text={error}
        icon="error"
        onConfirm={handleFetchError}
      />
    );
  };
  const options = { day: "numeric", month: "long", year: "numeric" }
  const submitHandler = async (event) => {
    event.preventDefault();
    if (reviewText === "") {
      return setSwalProps({
        show: true,
        title: "Error",
        text: "Please type your review before submitting.",
        icon: "error",
      });
    } else if (rating === null || rating === undefined) {
      return setSwalProps({
        show: true,
        title: "Error",
        text: "Please select a rating before submitting.",
        icon: "error",
      });
    } else {
      try {
        if (!user || user === undefined || user === null || user === "") {
          return setSwalProps({
            show: true,
            title: "Error",
            text: "Please SignIn.",
            icon: "error",
          });
        }
        const reviewObj = {
          username: user?.username,
          reviewText,
          rating,
        };
        const createReviewRes = await fetch(
          `${BASE_URL}/review/create-review/${id}`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            credentials: "include",
            body: JSON.stringify(reviewObj),
          }
        );
        const createReviewResult = await createReviewRes.json();
        if (!createReviewRes.ok) {
          return setSwalProps({
            show: true,
            title: "Error",
            text: createReviewResult.message,
            icon: "error",
          });
        }
        const fetchTourRes = await fetch(
          `${BASE_URL}/tour/get-single-tour/${id}`
        );
        const fetchTourResult = await fetchTourRes.json();
        if (!fetchTourRes.ok) {
          return setSwalProps({
            show: true,
            title: "Error",
            text: fetchTourResult.message,
            icon: "error",
          });
        }
        setReviews([...reviews, createReviewResult.data]);
        setSwalProps({
          show: true,
          title: "Success",
          text: "Review submitted successfully!",
          icon: "success",
        });
  
        // Reset the reviewText and rating states
        setReviewText(""); // Clear the review text
        setTourRating(null); // Deselect the rating stars
        const initialRatingIcons = [
          "ri-star-s-line",
          "ri-star-s-line",
          "ri-star-s-line",
          "ri-star-s-line",
          "ri-star-s-line",
        ];
        setRatingIcons(initialRatingIcons);
      } catch (error) {
        return setSwalProps({
          show: true,
          title: "Error",
          text: error.message,
          icon: "error",
        });
      }
    }
  };
  const handleAlertConfirm = () => {
    setSwalProps({ show: false }); // Reset the show property to hide the alert
  };
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <>
      <section>
        <Container>
          <Row>
            {loading ? (
              <Dna
                visible={true}
                height="200"
                width="300"
                ariaLabel="dna-loading"
                wrapperStyle={{}}
                wrapperClass="dna-wrapper"
              />
            ) : (
              <>
                <Col lg="8">
                  <div className="tour__content">
                    <img src={photo} alt={title} />
                    <div className="tour__info">
                      <h2>{title}</h2>
                      <div className="d-flex align-items-center gap-5">
                        <span className="tour__rating d-flex align-items-center gap-1">
                          <i
                            className="ri-star-fill"
                            style={{ color: "var(--secondary-color)" }}
                          ></i>
                          {avgRating === 0 ? null : avgRating}
                          {totalRating === 0 ? (
                            "N/A"
                          ) : (
                            <span>({reviews?.length})</span>
                          )}
                        </span>
                        <span>
                          <i className="ri-map-pin-user-fill"></i> {address}
                        </span>
                      </div>
                      <div className="tour__extra-details">
                        <span><i className="ri-map-pin-2-line"></i> {city}</span>
                        <span><i className="ri-money-dollar-circle-line"></i> ${price}/per person</span>
                        <span><i className="ri-map-pin-time-line"></i> {distance} k/m</span>
                        <span><i className="ri-group-line"></i> {maxGroupSize} people</span>
                      </div>
                      <h5>Description</h5>
                      <p>{desc}</p>
                    </div>
                    <div className="tour__reviews mt-4">
                      <h4>Reviews {reviews?.length} reviews</h4>
                      <Form onSubmit={submitHandler}>
                        <div className="d-flex align-items-center gap-3 mb-4 rating__group">
                          {ratingIcons && ratingIcons.length > 0 && ratingIcons.map((iconClass, index) => (
                            <span key={index} onClick={() => handleRatingClick(index)}>
                              <i className={iconClass}></i> {index + 1}
                            </span>
                          ))}
                        </div>
                        <div className="review__input">
                          <input type="text" placeholder="Share your thoughts" value={reviewText} onChange={(e) => setReviewText(e.target.value)} />
                          <button className="btn primary__btn text-white" type="submit">Submit</button>
                        </div>
                      </Form>
                      <ListGroup className="user__reviews">
                        {reviews && reviews.length > 0 ? (
                          <>
                            {reviews.map((review, index) => {
                              return (
                                <div className="review__item" key={review && review._id ? review._id : index }>
                                  <img src={avatar} alt="avatar" />
                                  <div className="w-100">
                                    <div className="d-flex align-items-center justify-content-between">
                                      <div>
                                        <h5>{review?.username}</h5>
                                        <p>{new Date(review.createdAt).toLocaleDateString("en-US", options)}</p>
                                      </div>
                                      <span className="d-flex align-items-center">
                                        {review.rating}<i className="ri-star-s-fill"></i>
                                      </span>
                                    </div>
                                    <h6>{review?.reviewText}</h6>
                                  </div>
                                </div>
                              )
                            })}
                          </>
                        ) : ""}
                      </ListGroup>
                    </div>
                  </div>
                </Col>
                <Col lg="4">
                  <Booking tour={tour} avgRating={avgRating} />
                </Col>
              </>
            )}
          </Row>
        </Container>
        <SweetAlert {...swalProps} onConfirm={handleAlertConfirm} />
        {showErrorAlert()}
      </section>
    </>
  );
};

export default TourDetails;
